import React from "react";
const commands={
  clear:'clear',
  en_live:'en_live',
  dis_live:'dis_live'
};
const UseOnEnter=()=>{
  const [consoleOutput,updateConsoleOutput]=React.useState([]);
  async function onEnter(value,set_live_en,key){
    if(key==="Enter"){
      const command=commands[value]||value;
      let result=<span/>;
      if(commands[value]==="clear"){
        return updateConsoleOutput(consoleOutput.filter(()=>false));
      }else if(commands[value]==="en_live"){
        set_live_en(true);
        return updateConsoleOutput(consoleOutput);
      }
      else if(commands[value]==="dis_live"){
        set_live_en(false);
        return updateConsoleOutput(consoleOutput);
      }
      let response=(await fetch('https://www.milad-karami.ir/do_command',{
        method:'POST',
        headers:{
          'Accept':'application/json',
          'Content-Type':'application/json',
        },
        body:JSON.stringify({
          command:command,
        })
      })).json();
      response= await response;
      result=<span>{response['response_message']}</span>;
      return updateConsoleOutput(consoleOutput=>
        consoleOutput.concat({command,result})
      );
    }
  }
  return [consoleOutput,onEnter];
};
export default UseOnEnter;
