import React from "react";
import {useState} from "react";
import Prompt from "./Prompt";
import UseOnEnter from "./UseOnEnter";
import MapConsoleOutput from "./MapConsoleOutput";
const Console=()=>{
  const inputText=React.useRef();
  const [consoleOutput,onEnter]=UseOnEnter();
  const [live_en,set_live_en]=useState(false);
  React.useEffect(()=>{
    inputText.current.value="";
    inputText.current.focus();
  });
  return (
    <section className="console">
      <MapConsoleOutput consoleOutput={consoleOutput}/>
      <div className="input-prompt">
        <Prompt/>
        <input
          type="text"
          ref={inputText}
          onKeyPress={({target:{value},key})=>onEnter(value,set_live_en,key)}
        />
      </div>
      {live_en?<div>
        <img src="https://www.milad-karami.ir/live" alt=""/>
      </div>:null}
    </section>
  );
};
export default Console;
